import React from "react";
import "./Home.css";
import logo from "../../IMG/logo.png";
import { TypeAnimation } from "react-type-animation";
import AOS from "aos";
import "aos/dist/aos.css";
import { useInView } from 'react-intersection-observer';

function Home() {
  const { ref: myRef, inView: myElementIsVisible } = useInView();
  const { ref: magicSectionRef, inView: magicSectionIsVisible } = useInView();
  return (
    <div className="home">
    <h2 className="ima">
      <TypeAnimation
            sequence={[
              "I'm a Web Developer",
              1000,
              "I'm a Mobile Developer",
              1000,
              "I'm a Ui/Ux Designer",
              1000,
            ]}
            repeat={Infinity}
          />
      </h2>
      <h1 ref={myRef} className={myElementIsVisible? 'anim' :''}>
        hi,I'm
        <br />{" "}
        <span>Shehara Dewanagala</span>
        <br />
        &lt;/&gt;
        <br /><br />
      </h1>
      
      <p>
        Welcome to my portfolio website! I am a skilled software developer and a
        proud graduate of
        <br />
        Rajarata University in Sri Lanka. With expertise in Java, Python, C++,
        and JavaScript,
        <br />
        I create innovative web and mobile applications. Explore my portfolio to
        see my dedication <br />
        to deliver high-quality software solutions.
      </p>

      <img src={logo} ref={myRef} className={myElementIsVisible? 'anim-r' :''} />
    </div>
  );
}

export default Home;
